export const goToOrderCenter = (data) => {
    let url = data.insuranceType === 'carInsurance' ?
        HTTP_BASE_PATH + '/QueryCarOrderUrl.do' : // 
        HTTP_BASE_PATH + '/QueryNonCarOrderUrl.do'; // 

    return fetchFun(url, data);
}

