export const vilidate = (obj, tip, top,viliCon,viliRule,bs,bol) => {
	if(!(viliRule.test(viliCon))) {
		//					this.$vux.toast.text('')	
		warn(obj, tip, top)
		obj[bs] = false;		
		return "";
				
	}
}

//
export const warn = (obj, tip, top) => {
	var that = obj;
	obj['warnTip'] = tip;
	obj['warnTop'] = top;
	obj['isShake'] = true;
	var timer = setTimeout(function() {
		that['isShake'] = false;
		clearTimeout(timer)
	}, 500)
}

